$alignmentIndent: 0;
$alignmentSpace: 50px;

/* Main Fixes */
.su-page-content {

    /* Images Alignment */
    .adaptiveimage .align-right {
        float: right;
        margin-left: $alignmentSpace;
        margin-right: 0;
        margin-bottom: 1em;
    }

    .adaptiveimage .align-left {
        float: left;
        margin-right: $alignmentSpace;
        margin-left: $alignmentIndent;
        margin-bottom: 1em;
    }

    .adaptiveimage .image-one-fourth-width {
        width: 25%;
    }

    .adaptiveimage .image-half-width {
        width: 50%;
    }

    .adaptiveimage .image-full-width,
    .adaptiveimage .image-two-third-width {
        width: 100%;
    }

    /* Images Caption */
    .cq-dd-image {

        margin-top: 3.8rem;

        .caption-container {
            font-family: "Source Sans 3", "Source Sans Pro", "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-size: 1.6rem;
            line-height: 1.2em;
            font-weight: 400;
            margin: 0 auto;
            /* color: rgb(109, 108, 105); */
            color: #000;
            max-width: 633px;
            text-align: left;
            clear: both;
            padding-top: 0.4em;
            font-style: normal;
            display: flex;
            justify-content: center;
        }

        img {
            margin-bottom: 0px !important;
        }
    }

    /* Quote Styling */
    blockquote.large::before {
        content: "“";
        line-height: 139.5px;
        font-size: 93px;
        margin-top: -3.8rem;
        font-weight: 600;
        color: rgb(46, 45, 41);
        position: absolute;
        left: 0;
    }

    blockquote.large {
        position: relative;
        font-family: "Source Serif 4", "Source Serif Pro", Georgia, Times, "Times New Roman", serif;
        font-weight: 600;
        font-size: 3.6rem;
        color: rgb(46, 45, 41);
        width: 100%;
        line-height: 46.89px;
        margin-top: 7.6rem;
        padding-left: 5.2rem;
        margin-bottom: 7.6rem;
    }

    blockquote.large::after {
        content: "”";
    }

    @media (max-width: 767px) {

        /* Image Caption */
        .cq-dd-image {
            .caption-container {
                max-width: 582px;
                padding-left: 7.5px;
                padding-right: 7.5px;
            }
        }

        /* Image Alignment */
        .adaptiveimage .align-right {
            margin-right: 0;
        }

        .adaptiveimage .align-left {
            margin-left: 0;
        }

        /* Quote Media */
        blockquote.large {
            margin-top: 3.8rem;
            padding-left: 3.9rem;
            margin-bottom: 3.8rem;
            line-height: 33.6px;
            font-size: 2.4rem;
        }

        blockquote.large::before {
            line-height: 109.5px;
            font-size: 73px;
            margin-right: 0.6rem;
            margin-top: -2.5rem;
        }

    }

    @media (max-width: 575.98px) {

        .adaptiveimage .align-right,
        .adaptiveimage .align-left {
            margin: 0 auto;
            float: none;
            max-width: 100%;
        }

        .adaptiveimage .image-one-fourth-width,
        .adaptiveimage .image-half-width {
            width: 50%;
        }

    }

}

/* Dark Mode fixes */
.su-dark {
    .su-page-content {

        /* Image Captions */
        .cq-dd-image {
            .caption-container {
                color: #FFF;
            }
        }

        /* Quote */
        blockquote.large,
        blockquote.large::before,
        blockquote.large::after {
            color: #FFF;
        }
    }
}